<template>
  <div class="flex_column">
    <div class="section">
      <zle-form ref="form" class="flex_b_center" :inline="true" :model="addForm" size="mini" label-width="80px">
        <zle-form-item label="集合编号">
          <zle-input v-model.trim="addForm.code" class="search-input" placeholder="请输入集合编号" />
        </zle-form-item>
        <zle-form-item label="集合名称">
          <zle-input v-model.trim="addForm.name" class="search-input" placeholder="请输入集合编号" />
        </zle-form-item>
        <zle-form-item label="内容载体">
          <zle-select v-model="addForm.carrier" class="search-input" placeholder="请选择">
            <zle-option label="全部" value="全部" />
          </zle-select>
        </zle-form-item>
        <zle-form-item>
          <zle-button icon="el-icon-search" type="primary">查询</zle-button>
        </zle-form-item>
      </zle-form>
      <zle-form class="flex_b_center" :inline="true" :model="addForm" size="mini" label-width="80px">
        <zle-form-item label="筛选方式">
          <div class="search-input">
            <zle-select v-model="addForm.filter" class="search-input" placeholder="请选择">
              <zle-option label="全部" value="全部" />
            </zle-select>
          </div>
        </zle-form-item>
        <zle-form-item label="创建时间">
          <zle-date-picker v-model="addForm.creationTime" style="width: 238px" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" />
        </zle-form-item>
        <zle-form-item label="状态">
          <zle-select v-model="addForm.state" class="search-input" placeholder="请选择">
            <zle-option label="全部" value="全部" />
          </zle-select>
        </zle-form-item>
        <zle-form-item>
          <zle-button icon="el-icon-refresh-left" type="info">重置</zle-button>
        </zle-form-item>
      </zle-form>
    </div>
    <div class="section flex_1_auto flex_column">
      <div style="margin-bottom: 6px">
        <zle-button icon="el-icon-plus" size="mini" type="primary">新建</zle-button>
        <zle-button size="mini">批量操作</zle-button>
        <zle-button size="mini">更多操作<i class="el-icon-arrow-down el-icon--right" /></zle-button>
      </div>
      <div class="flex_b_center statistics">
        <div class="flex_a_center">
          <i class="el-icon-warning" style="font-size: 15px; color: #1890ff; margin-right: 9px" />
          <div style="margin-right: 14px">
            已选择
            <span style="color: #1890ff">{{ '&nbsp;' + multipleSelection.length + '&nbsp;' }}</span>
            项
          </div>
          <div style="margin-right: 26px">
            服务调用总计：<span>{{ plus() }}</span>
          </div>
          <zle-button type="text" @click="clear">清空</zle-button>
        </div>
        <div>
          <i class="el-icon-refresh-right icon" style="margin-right: 10px" @click="reset" />
          <el-popover placement="bottom" trigger="hover">
            <div class="flex_c_center">
              <zle-checkbox v-model="columnShow.codeColShow">规则编号</zle-checkbox>
              <zle-checkbox v-model="columnShow.describeColShow">描述</zle-checkbox>
              <zle-checkbox v-model="columnShow.usedNumberColShow">服务调用次数</zle-checkbox>
              <zle-checkbox v-model="columnShow.stateColShow">状态</zle-checkbox>
              <zle-checkbox v-model="columnShow.updateTimeColShow">更新时间</zle-checkbox>
              <zle-checkbox v-model="columnShow.operationColShow">操作</zle-checkbox>
            </div>
            <i slot="reference" class="el-icon-setting icon" />
          </el-popover>
        </div>
      </div>
      <div class="flex_1_auto">
        <zle-table ref="multipleTable" :data="tableData" height="100%" @selection-change="handleSelectionChange">
          <zle-table-column type="selection" width="55" align="center" />
          <zle-table-column v-if="columnShow.codeColShow" prop="code" label="规则编号" width="150" align="center" />
          <zle-table-column v-if="columnShow.describeColShow" prop="describe" label="描述" align="center" />
          <zle-table-column v-if="columnShow.usedNumberColShow" prop="usedNumber" label="服务调用次数" align="center" sortable />
          <zle-table-column v-if="columnShow.stateColShow" prop="state" width="120" label="状态" align="center" :filters="[ { text: '关闭', value: '关闭' }, { text: '运行中', value: '运行中' }, { text: '已上线', value: '已上线' }, { text: '异常', value: '异常' } ]" :filter-method="filterState" />
          <zle-table-column v-if="columnShow.updateTimeColShow" prop="updateTime" label="更新时间" align="center" sortable />
          <zle-table-column v-if="columnShow.operationColShow" label="操作" width="150" align="center">
            <zle-button type="text">编辑</zle-button>
            <zle-button type="text">订阅警报</zle-button>
          </zle-table-column>
        </zle-table>
      </div>
      <zle-pagination class="my_page" :page.sync="searchParam.current" :size.sync="searchParam.size" :total="total" @pagination="changePage" />
    </div>
  </div>
</template>

<script>
import { Popover } from 'element-ui'
// import { mapGetters } from 'vuex'
export default {
  components: {
    [Popover.name]: Popover
  },
  data() {
    return {
      addForm: {
        name: '',
        code: '',
        carrier: '',
        filter: '',
        creationTime: '',
        state: ''
      },
      columnShow: {
        codeColShow: true,
        describeColShow: true,
        usedNumberColShow: true,
        stateColShow: true,
        updateTimeColShow: true,
        operationColShow: true
      },
      select: '全部',
      radio: '全部工单',
      searchParam: {
        current: 1,
        size: 10,
        origin: '',
        name: ''
      },
      total: 100, //
      tableData: [
        {
          code: 'TradeCode 0',
          describe: '这是一段描述，关于这个应用的描述文字内容',
          updateTime: '2022-09-10 08:15:42',
          usedNumber: '359万',
          state: '关闭'
        },
        {
          code: 'TradeCode 1',
          describe: '这是一段描述，关于这个应用的描述文字内容',
          updateTime: '2022-04-15 08:15:42',
          usedNumber: '194万',
          state: '运行中'
        },
        {
          code: 'TradeCode 4',
          describe: '这是一段描述，关于这个应用的描述文字内容',
          updateTime: '2022-04-15 08:15:42',
          usedNumber: '591万',
          state: '运行中'
        },
        {
          code: 'TradeCode 2',
          describe: '这是一段描述，关于这个应用的描述文字内容',
          updateTime: '2022-12-15 08:15:42',
          usedNumber: '932万',
          state: '异常'
        }
      ],
      multipleSelection: []
    }
  },
  computed: {
    // ...mapGetters([])
  },
  methods: {
    changePage(val) {
      console.log(val)
    },
    reset() {
      this.columnShow = this.$options.data().columnShow
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    plus() {
      let totalUsedNumber = this.multipleSelection.reduce((accumulator, currentValue) => {
        let usedNumber = parseInt(currentValue.usedNumber.replace(/万/g, ''))
        return accumulator + usedNumber
      }, 0)
      let totalUsedNumberString = totalUsedNumber + '万'
      return totalUsedNumberString
    },
    filterState(value, row) {
      return row.state === value
    },
    clear() {
      this.$refs.multipleTable.clearSelection()
    }
  }
}
</script>

<style lang="scss" scoped>
.search-input {
  width: 238px;
}
.icon {
  font-size: 24px;
}
.flex_c_center {
  width: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.statistics {
  height: 40px;
  background-color: #e6f7ff;
  border: 1px solid #bae7ff;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 0 17px;
}
.flex_c_center {
  width: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.flex_1_auto {
  flex: 1;
  overflow: auto;
}
.flex_column {
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.flex_a_center {
  display: flex;
  align-items: center;
}
.flex_b_center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

</style>
